.main3content{
    font-family: serif;
    font-weight: 600;
    line-height: 1;

    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;

    margin-bottom: -110rem;

    background-image: url(./topo.png);
    background-size: 80%;
    background-repeat: no-repeat;
    background-position-x: 100%;
}

.main3content__title{
    text-align: justify;
    position: relative;
    font-size: 4.5rem;
}

.main3content__text{
    font-size: 1.5rem;
    color: var(--color-grey-dark);
    line-height: 2.5rem;

    margin-top: 4rem;
    margin-right: 30rem;
}

.main3content__text-button{
    margin-top: 6rem;
    color: black;
    letter-spacing: .2rem;
    cursor: pointer;
}

@media only screen and (max-width: 885px){
    .main3content{
        grid-column: 2 / 4;
        grid-row: 1 / 2;

        display: flex;
        align-items: center;

        background-image: none;
    }

    .main3content__text{
        color: black;
        margin-right: 0rem;
    }

    .main3content__title{
        text-align: center;
    }
}